@import '../styles.scss';

.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #14181C;
    margin: 0;
    padding: 0 0 20px 50px;
    height: 13vh;
    position: sticky;
    top: 0;
    z-index: 2;
}

.heading {
    text-decoration: none;
    margin: 0;
    padding: 20px 0 10px 0;
    font-family: 'Tiro Devanagari Hindi', serif;
    font-size: 2.2em;
    letter-spacing: .05em;
    color: #fff;
}

.navbar-home,
.search-by-title-submit {
    margin: 10px 50px 0 0;
    padding: 7px 15px;
    cursor: pointer;
    font-family: sans-serif;
    font-size: 1em;
    letter-spacing: .1em;
    border: none;
    color: #fff;
    opacity: 1;
    background-color: $green;
    border-radius: 20px;
    &:disabled,
    &:disabled:hover {
        cursor: not-allowed;
        background-color: $dark-grey;
    }
    &:hover {
        background-color: #019526;
    }
}

.navbar-home {
    text-decoration: none;
    align-self: flex-end;
}

.form-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.search-by-title-input {
    margin-right: 20px;
    padding: 7px 15px;
    border-radius: 20px;
    border: none;
}

